import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export const store= new Vuex.Store({
	state:{
		LogisShow: null,//ç™»é™†ç•Œé¢æ˜¾ç¤ºæ¡ä»¶ ï¼Œapp.vueä¸­ä½¿ç”¨
		RegisShow: null,//æ³¨å†Œé¡µé¢æ˜¾ç¤ºæ¡ä»¶ ï¼Œapp.vueä¸­ä½¿ç”¨
		nav:null, //åˆ‡æ¢ç™»é™†æ³¨å†Œçš„å¯¼èˆªçŠ¶æ€ï¼Œç™»é™†æ³¨å†Œç»„ä»¶æœ‰ä½¿ç”¨
		success:null,//app.vueä¸­æ¶ˆæ¯æç¤ºçŠ¶æ€
		msg:'',//app.vueä¸­æ¶ˆæ¯æç¤ºçš„ä¿¡æ¯å†…å®¹
		phone:'', //å½“å‰ç™»é™†çš„ç”¨æˆ·
		loginstatus:null,//ç™»é™†çŠ¶æ€
		headshot:"",//å½“å‰ç™»å½•ç”¨æˆ·çš„å¤´åƒä¿¡ä¿¡æ¯
		UserItems:[],//adminä¸­ä½¿ç”¨åˆ°,æ³¨å†Œæ—¶å€™æˆ–è€…åœ¨adminæ·»åŠ ç”¨æˆ·æ—¶å€™å†™å…¥stateé‡Œç”¨æˆ·ä¿¡æ¯
		cid:"",//å½“å‰ç”¨æˆ·çš„id
		ctype:"",//å½“å‰ç”¨æˆ·çš„ç±»åž‹ï¼Œ0ä¸ºæ™®é€šç”¨æˆ·ï¼Œ1ä¸ºç®¡ç†å‘˜
		filteredBlogs:[],//searchblogsç»„ä»¶éœ€è¦çš„æ•°æ®ï¼Œç‚¹å‡»æœç´¢æŒ‰é’®æ—¶å€™ä¼ å…¥è¯¥æ•°æ®
		
	},
	getters:{
		getLoginstatus: state=>state.loginstatus,
		getAccount: state=>state.phone
	},
	mutations:{
		setPopLog(state,data){
			state.LogisShow = data.LogisShow
			state.nav = data.nav
		},
		setPopReg(state,data){
			state.RegisShow = data.RegisShow
			state.nav = data.nav

		},
		userAction(state,data){
			state.loginstatus = data.loginstatus
			state.phone = data.phone
			state.ctype = data.ctype
			state.cid = data.cid
			state.headshot = data.headshot
		},
		popMessage(state,data){
			state.success = data.success
			state.msg = data.msg
		},
		setUserItems(state,data){
			state.UserItems = data
		},
		addUserItems(state,data){
			state.UserItems.push(data)
		},
		deleteUserItems(state,data){
			state.UserItems.splice(state.UserItems.indexOf(data),1)
		},
		getSearchBlogs(state,data){
			state.filteredBlogs = data
		},
		setHeadShot(state,data){
			state.headshot = data
		}
		
		

	},
	actions:{

	}
})



