	window.checkData = function(type,data,nec){
	
		let func = {
			checkIdcard: function (idCard){
				idCard = idCard.replace(/(^\s*)|(\s*$)/g, "");
				let Errors=["éªŒè¯é€šè¿‡!", "èº«ä»½è¯å·ç é”™è¯¯!", "èº«ä»½è¯æ ¼å¼ä¸æ­£ç¡®!"]

				//15ä½å’Œ18ä½èº«ä»½è¯å·ç çš„æ­£åˆ™è¡¨è¾¾å¼
				let regIdCard=/^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;

				//å¦‚æžœé€šè¿‡è¯¥éªŒè¯ï¼Œè¯´æ˜Žèº«ä»½è¯æ ¼å¼æ­£ç¡®ï¼Œä½†å‡†ç¡®æ€§è¿˜éœ€è®¡ç®—
				if(regIdCard.test(idCard)){
				  	if(idCard.length==18){
						let idCardWi=new Array( 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2 ); //å°†å‰17ä½åŠ æƒå› å­ä¿å­˜åœ¨æ•°ç»„é‡Œ
						let idCardY=new Array( 1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2 ); //è¿™æ˜¯é™¤ä»¥11åŽï¼Œå¯èƒ½äº§ç”Ÿçš„11ä½ä½™æ•°ã€éªŒè¯ç ï¼Œä¹Ÿä¿å­˜æˆæ•°ç»„
						let idCardWiSum=0; //ç”¨æ¥ä¿å­˜å‰17ä½å„è‡ªä¹–ä»¥åŠ æƒå› å­åŽçš„æ€»å’Œ
						for(let i=0;i<17;i++){
							idCardWiSum+=idCard.substring(i,i+1)*idCardWi[i];
						}

						let idCardMod=idCardWiSum%11;//è®¡ç®—å‡ºæ ¡éªŒç æ‰€åœ¨æ•°ç»„çš„ä½ç½®
						let idCardLast=idCard.substring(17);//å¾—åˆ°æœ€åŽä¸€ä½èº«ä»½è¯å·ç 

					    //å¦‚æžœç­‰äºŽ2ï¼Œåˆ™è¯´æ˜Žæ ¡éªŒç æ˜¯10ï¼Œèº«ä»½è¯å·ç æœ€åŽä¸€ä½åº”è¯¥æ˜¯X
					    if(idCardMod==2){
							if(idCardLast=="X"||idCardLast=="x"){
								return {
								  	status: true,
								  	msg: Errors[0]
								};
							}else{
								return {
								  	status: false,
								  	msg: Errors[1]
								};
							}
					    }else{
							//ç”¨è®¡ç®—å‡ºçš„éªŒè¯ç ä¸Žæœ€åŽä¸€ä½èº«ä»½è¯å·ç åŒ¹é…ï¼Œå¦‚æžœä¸€è‡´ï¼Œè¯´æ˜Žé€šè¿‡ï¼Œå¦åˆ™æ˜¯æ— æ•ˆçš„èº«ä»½è¯å·ç 
							if(idCardLast==idCardY[idCardMod]){
								return {
								  	status: true,
								  	msg: Errors[0]
								};
							}else{
								return {
								  	status: false,
								  	msg: Errors[1]
								};
							}
					    }
					}
				}else{
					return {
						status: false,
						msg: Errors[2]
					};
				}
			},

			checkPhone: function (sMobile) {
				if(!(/0?(13|14|15|18)[0-9]{9}/.test(sMobile))){ 
				    return {
				    	status: false,
					  	msg: "æ‰‹æœºå·ç æ ¼å¼é”™è¯¯ï¼"
				    }; 
				}else{
					return {
				    	status: true,
					  	msg: ""
				    }; 
				}
			},
			checkEmail: function (Email){
				if(!(/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(Email))){ 
				    return {
				    	status: false,
					  	msg: "é‚®ç®±æ ¼å¼é”™è¯¯ï¼"
				    }; 
				}else{
					return {
				    	status: true,
					  	msg: ""
				    }; 
				}
			},
			checkAccount: function (Account){
				if(!(/0?(13|14|15|18)[0-9]{9}/.test(Account) ||
					/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(Account))){ 
				    return {
				    	status: false,
					  	msg: "æ‰‹æœºå·æˆ–é‚®ç®±æ ¼å¼é”™è¯¯ï¼"
				    }; 
				}else{
					return {
				    	status: true,
					  	msg: ""
				    }; 
				}
			},
			checkDataEmpty: function(data, nec){
				if(!(nec instanceof Array)) return {status: true,msg: ""};
				for (let i= 0; i< nec.length; i++) {
				 	for (let j in data) {
					    if(j === nec[i]){
						    if (data[j] === ""){
						        return {
							    	status: false,
								  	msg: "å¿…å¡«é¡¹ä¸èƒ½ä¸ºç©º!"
							    }
						    }
					    }
				  	}
				}
				return {status: true,msg: ""};
			}
		}

		if(type === 'idcard'){
		    return func.checkIdcard(data)
		}else if(type === 'phone'){
			return func.checkPhone(data)
		}else if(type === 'complement'){
			return func.checkDataEmpty(data,nec)
		}else if(type === 'email'){
			return func.checkEmail(data)
		}else if(type === 'account'){
			return func.checkAccount(data)
		}else{
			return {
		    	status: true,
			  	msg: ""
		    };
		}
			
	}
